/* ========================================
   CSS RESET &  
    reset   
   ======================================== */

/* Box model    */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/*     */
* {
    margin: 0;
    padding: 0;
    border: 0;
}

/*   body -    padding-top */
body {
    margin: 0;
    border: 0;
    /* padding    header.css */
}

/*     */
html {
    scroll-behavior: smooth;
}

main,
.main-content {
    position: relative;
    z-index: 1;
}

.header {
    z-index: 100;
}

/* HTML  Body  */
html {
    font-size: 16px;
    height: 100%;
    overflow-x: hidden; /*    */
}

body {
    min-height: 100vh;
    line-height: 1.6;
    width: 100%;
    max-width: 100vw;
    overflow-x: hidden; /*    */
    font-family: var(--font-primary);
    color: var(--color-text-primary);
    background-color: var(--color-bg-primary);
    overflow-x: hidden;
}

/*    */
body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

/*  */
ul, ol {
    list-style: none;
}

/*  */
a {
    text-decoration: none;
    color: inherit;
    transition: all 0.3s ease;
}

/*  */
img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
    height: auto;
}

/*  */
input, button, textarea, select {
    font: inherit;
    background: transparent;
    border: none;
    outline: none;
}

button {
    cursor: pointer;
}

/*  */
table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*    */
:focus-visible {
    outline: 2px solid var(--color-accent);
    outline-offset: 2px;
}
